import * as Yup from 'yup'

const phoneNumRegex = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/

const schemaObject = {
  tiekunta: Yup.string()
    .min(3)
    .max(50)
    .required(),
  kunta: Yup.string()
    .min(2)
    .max(30)
    .required(),
  kayttooikeusyksikkotunnus: Yup.string()
    .max(30)
    .optional(),
  ilmoittaja: Yup.string()
    .min(5)
    .max(40)
    .required(),
  puhelinnumero: Yup.string().matches(phoneNumRegex),
  email: Yup.string()
    .email()
    .optional(),
  ilmoittajanSuhde: Yup.string().optional()
}

const initialValues = {
  tiekunta: '',
  kunta: '',
  kayttooikeusyksikkotunnus: '',
  ilmoittaja: '',
  puhelinnumero: '',
  email: '',
  ilmoittajanSuhde: ''
}

// Page schema for one page at a time validation
export const schema = Yup.object().shape(schemaObject)
export const schemaAsArray = Object.keys(schemaObject)

export default { schema, schemaAsArray, initialValues, schemaObject }
