import React from 'react'
import { Form, Row, Col } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import InfoSybmol from './utils/InfoSymbol'

const CheckBoxInput = props => {
  const { t } = useTranslation()
  const { label, info } = props
  const { field, form } = props
  const { errors, touched } = form

  const error = errors[field.name] && touched[field.name] ? errors[field.name] : false
  const isValid = !error && touched[field.name]

  return (
    <Form.Group>
      {info && (
      <Row>
        <Col>
          <Form.Label>
          </Form.Label>
        </Col>
          <Col className="col-auto">
            <InfoSybmol title={label} content={info} />
          </Col>
      </Row>
       )}
      <Row>
        <Col xs={11}>
          <Form.Check
            {...props}
            {...field}
            // double exclamation mark covers null value which causes non-controlled error
            checked={!!field.value} 
            isInvalid={error}
            isValid={isValid}
            label={t(label)}
          ></Form.Check>
        </Col>
      </Row>
    </Form.Group>
  )
}

export default CheckBoxInput
